.loginInput {
    border: 1px solid #dddfe2;
    width: -webkit-fill-available;
    margin-bottom: 15px;
    margin-right: 0px;
}
.loginForm {
    width: 40%;
}
.rememberMe {
    display: flex;
    margin-bottom: 15px;
}
.rememberMe input {
    margin-right: 10px;
}
.loginButton {
    width: 100%;
    height: 40px;
}
.captcha {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 15px;
}
.loginPage {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 500px;
}
.smile {
    width: 130px;
    height: 130px;
    background: #0a98dc;
    border-radius: 100%;
    border: 12px solid #23a3e0;
    margin: auto;
    position: relative;
}
.smile img {
    position: absolute;
    left: 15px;
    top: 15px;
}
.greeting {
    margin-top: 10px;
}
.greetingText {
    text-align: center;
    margin-bottom: 30px;
    font-size: 38px;
    color: darkblue;
}
.greetingText img {
    width: 120px;
}